.d-flex{
    display: flex;
}

.d-block {
    display: block;
}

.f-column{
    flex-direction: column;
}

.f-row {
    flex-direction: row;
}

.h-200{
    height: 200px;
}

.w-fit-content{
    width: fit-content;
}

.m-auto {
    margin: auto;
}

.resizer {
    box-sizing: border-box;
    background-clip: padding-box;
    background-color: grey;
}

.resizer-vertical {
    min-height: 12px;
    margin: -5px 0;
    border-top: 5px solid transparent;
    border-bottom: 5px solid transparent;
    cursor: row-resize;
}

.resizer-horizontal {
    min-width: 12px;
    margin: 0 -5px;
    border-left: 5px solid transparent;
    border-right: 5px solid transparent;
    cursor: col-resize;
}

.resizer:hover{
    background-color: whitesmoke;
}


.full-page-class {
    display: block;
    height: 100%;
    position: fixed;
    top: 0px;
    left: 0px;
    right: 1000px;
    bottom: 100px;
    width: 100%;
}


.h-20 {
    height: 20px;
}

.w-20 {
    width: 20px;
}

.overflow-hidden {
    overflow: hidden;   
   }


.w-100p{
    width: 100%
}

.h-100p{
    height: 100%;
}

.flex-shrink-0{
    flex-shrink: 0;
